import { Component } from '@angular/core';
import { IonicPage, NavController,NavParams, LoadingController, ToastController, AlertCmp, AlertController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'change-password1',
  segment: 'change-password1'
})

@Component({
  selector: 'change-password1',
  templateUrl: 'change-password1.html'
})

export class ChangePassword1Page {
  public onChangePasswordForm: FormGroup;
  showPasswordText: boolean = false;
  // password:any;
  newone: any;
  tag:any;
  email:any;
  password:any;
  confirmpassword:any;

  constructor(public navCtrl: NavController, public navparam: NavParams, public translate: TranslateService, private _fb: FormBuilder, public forgotCtrl: AlertController, public user: UserProvider, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

    // this.userinfo();
    this.translate.setDefaultLang('en');
    this.translate.use('es');
   

    this.email = this.navparam.get('email');
    this.tag = this.navparam.get('tags');
    console.log(this.email, this.tag);




  }
  // show password text and "*******"
  managePassword() {

    this.showPasswordText = !this.showPasswordText;
    this.newone = this.onChangePasswordForm.value.newpassword;
  }
  // update password 
  updatePassword() {

    this.translate.get('Auth-page').subscribe((translated: string) => {
      
      var loader = this.translate.instant('Auth-page.loader');



    let loading = this.loadingCtrl.create({
      content: loader,
    });

    loading.present();

    this.user.doPasswordReset(this.email, this.tag).subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        // let data2 = JSON.parse(datas._body);
        console.log(datas);
        

        if (datas.success === true) {

          loading.dismiss();

          this.translate.get('reset-password1.list.success_toast').subscribe((translated: string) => {
            var toast1 = this.translate.instant('reset-password1.list.success_toast');


          let toast = this.toastCtrl.create({
            message: toast1,
            duration: 3000,
            position: 'top'
          });

          toast.onDidDismiss(() => {
            console.log('Dismissed toast');
            this.navCtrl.setRoot('page-auth');
            



          });
          toast.present();
        })

        } else{

          loading.dismiss();

          this.translate.get('reset-password1.list.error_toast_message').subscribe((translated: string) => {
            var toast1 = this.translate.instant('reset-password1.list.error_toast_message');

          let toast = this.toastCtrl.create({
            message: toast1,
            duration: 3000,
            position: 'top'
          });

          toast.onDidDismiss(() => {
            console.log('Dismissed toast');
            this.password = null;
            this.confirmpassword = null;
            



          });
          toast.present();

        })


        }


       

      }, error => {
        console.log(error.JSON);
					setTimeout(() => {
            loading.dismiss();
						this.navCtrl.setRoot('page-general_error');
					}, 1000);

      },
      () => {


      }
    )

    })

   

  }


}



